import React, { Component } from 'react';
import { Card, CardTitle, Collapse, CardBody, Button } from 'reactstrap';
import { Link } from 'react-scroll';
import icons from '../assets/icons.js';
import projects from '../assets/projects.js';
import wkLogo from '../assets/images/wkLogo.jpg';
import slyLogo from '../assets/images/slyLogo.jpg';
import certificates from '../assets/certificates.js'
import reactNative from '../assets/images/reactNative.jpg';
import react from '../assets/images/react.jpg';
import bootstrap from '../assets/images/bootstrap.jpg';
import machineLearning from '../assets/images/machineLearning.jpg';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showDetails: 0,
            showCertificate: 0,
            projectImages: {
                wkLogo: wkLogo,
                slyLogo: slyLogo
            },
            certificates: {
                reactNative: reactNative,
                react: react,
                bootstrap: bootstrap,
                machineLearning: machineLearning
            },
            active: "whoAmI",
            isMenuOpen: false
        }
    }

    componentDidMount() {
        window.addEventListener(
            "scroll",
            () => {
                document.body.style.setProperty(
                    "--scroll",
                    window.pageYOffset / document.getElementById("invisible-div").offsetHeight
                );
                document.body.style.setProperty(
                    "--progress",
                    window.pageYOffset / (document.body.offsetHeight - window.innerHeight)
                );
            },
            false
        );
    }

    toggleMenu = () => {
        this.setState({
            isMenuOpen: !this.state.isMenuOpen
        }, () => {
            if (this.state.isMenuOpen) {
                document.addEventListener("click", this.closeMenu);
            }
        });
    }

    closeMenu = (e) => {
        if (!document.getElementById("menu").contains(e.target)) {
            this.setState({
                isMenuOpen: false
            });
            document.removeEventListener("click", this.closeMenu);
        }
    }

    showDetails = (id) => {
        this.setState({
            showDetails: this.state.showDetails === id ? 0 : id
        });
    }

    showCertificate = (id) => {
        this.setState({
            showCertificate: this.state.showCertificate === id ? 0 : id
        });
    }

    renderIcons = () => (
        icons.map((icon) =>
            <div key={icon.id} className="col-6 col-md-4 my-3">
                <Card className="shadow">
                    <svg viewBox="0 0 128 128">{
                        icon.id === 5 ? <circle fill="#61DAFB" cx="64" cy="64" r="11.4"></circle> : ""
                    }
                    {
                        icon.path.map((path) =>
                            <path fill={path.fill} d={path.d}></path>
                        )
                    }</svg>
                    <CardTitle><h5>{icon.title}</h5><span className="fa fa-plus-circle fa-lg" style={this.state.showDetails === icon.id ? {transform: "rotate(45deg)", color: icon.path[0].fill} : {color: icon.path[0].fill}} onClick={() => this.showDetails(icon.id)}></span></CardTitle>
                    <Collapse isOpen={this.state.showDetails === icon.id}>
                        <CardBody>{
                            icon.description
                        }</CardBody>
                    </Collapse>
                </Card>
            </div>
        )
    );

    renderProjects = () => (
        projects.map((project) =>
            <div className="col-12 my-3">
                <Card className="shadow">
                    <div className="row m-0">
                        <div className="col-12 col-md-3 d-flex align-items-center">
                            <img className="project-logo" src={this.state.projectImages[project.logo]} alt={project.title} />
                        </div>
                        <div className="col-12 col-md-9">
                            <CardTitle>
                                <h5>{project.title}</h5>
                            </CardTitle>
                            <CardBody className="pt-0">
                                {project.description}
                                <a href={project.link} target="_blank" rel="noopener noreferrer">Take a look <span className="fa fa-link"></span></a>
                            </CardBody>
                        </div>
                    </div>
                </Card>
            </div>
        )
    );

    renderCertificates = () => (
        certificates.map((certi) =>
            <Card>
                <div className="card-header">
                    <div className="btn accordion-btn" onClick={() => this.showCertificate(certi.id)}>
                        <CardTitle className="p-0"><h5 className="accordion">{certi.title}</h5></CardTitle>
                    </div>
                </div>
                <Collapse isOpen={this.state.showCertificate === certi.id}>
                    <CardBody className="p-0">
                        <img className="certificate" src={this.state.certificates[certi.certificate]} alt={certi.title} />
                    </CardBody>
                </Collapse>
            </Card>
        )
    );

    render() {
        return (
            <>
                <div id="progress-bar"></div>
                <div id="invisible-div">
                    <div id="animated-container" className="container d-flex justify-content-center align-items-center" >
                        <div className="row w-100">
                            <div id="dev-col" className="col d-flex pr-0">
                                <span id="dev-span"><span id="em-span">ekagra-midha.</span>dev</span>
                            </div>
                            <div className="col pl-0">
                                <span id="elop">elop</span>
                                <span id="ise">ise</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="header" className="d-flex justify-content-center align-items-center">
                    ekagra-midha.dev 
                </div>
                <div id="side-menu" className="d-md-none" style={this.state.isMenuOpen ? {right: "0"} : {right: "-75vw"}}>
                    <div id="side-menu-toggle" onClick={this.toggleMenu}>
                        <span id="chevron-left" className="fa fa-chevron-left" style={this.state.isMenuOpen ? {transform: "rotate(180deg)"} : {}}></span>
                    </div>
                    <div id="menu" style={this.state.isMenuOpen ? {boxShadow: "0 1rem 3rem 0.25rem rgba(0,0,0,1)"} : {}}>
                        <div className="container pt-2 pl-4">
                            <div className="row">
                                <Link className="navbar-item" activeClass="active" to="whoAmI" spy={true} smooth={true} duration={1000}>
                                    Who am I?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="education" spy={true} smooth={true} duration={1000}>
                                    My education?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="achievements" spy={true} smooth={true} duration={1000}>
                                    My achievements?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="whatDoIDo" spy={true} smooth={true} duration={1000}>
                                    What do I do?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="howDoIDoIt" spy={true} smooth={true} duration={1000}>
                                    How do I do it?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="experience" spy={true} smooth={true} duration={1000}>
                                    How experienced am I?
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="main-container" className="container">
                    <div className="row">
                        <div className="col-12 col-md-9">
                            <div className="row pt-5" id="whoAmI">
                                <div className="col-12">
                                    <h2>Who am I?</h2>
                                    <p>
                                        As you might have guessed, I am Ekagra Midha; a software engineer who will be receiving his degree in mid-2021.
                                        Born and brought up in Delhi, I thrive on coding. You can call me web-development enthusiast (cliche, isn't it?),
                                        but it is not what I am limited to. I never learnt to procrastinate, nor can I overlook the minute details of any
                                        task assigned to me.
                                    </p>
                                </div>
                            </div>
                            <div className="row pt-5" id="education">
                                <div className="col-12">
                                    <h2>My education?</h2>
                                    <p>
                                        Let me summarise this for you in the following table.
                                    </p>
                                    <Card className="p-3 shadow">
                                        <div style={{overflowX: "scroll"}}>
                                            <table>
                                                <thead>
                                                    <tr>
                                                        <th>Year</th>
                                                        <th>Course</th>
                                                        <th>Institution</th>
                                                        <th style={{borderRight: "none"}}>Result</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td>2017-2021</td>
                                                        <td>Bachelor of Technology (CSE)</td>
                                                        <td>Maharaja Agrasen Institute of Technology, GGSIPU</td>
                                                        <td style={{borderRight: "none"}}>8.2 CGPA</td>
                                                    </tr>
                                                    <tr>
                                                        <td>2017</td>
                                                        <td>Senior Secondary Education (CBSE)</td>
                                                        <td>St. Margaret Sr. Sec. School, Delhi</td>
                                                        <td style={{borderRight: "none"}}>95.4%</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </Card>
                                </div>
                            </div>
                            <div className="row pt-5" id="achievements">
                                <div className="col-12">
                                    <h2>My achievements?</h2>
                                    <p>
                                        <ul>
                                            <li>Awarded with the title of All Rounder student for outstanding performance in scholastic and co-scholastic areas</li>
                                            <li>Received scholarships for being subject topper in Computer Science and Chemistry in Class XII Boards</li>
                                            <li>Bestowed with 7 Merit scholarships for 7 consecutive years for excellent academic performance in Classes VI-XII</li>
                                        </ul>
                                    </p>
                                </div>
                            </div>
                            <div className="row pt-5" id="whatDoIDo">
                                <div className="col-12">
                                    <h2>What do I do?</h2>
                                    <p>
                                        Well, the initial frames of the website sum this up. I devise (rather than develop) responsive frontend for web-applications.
                                        Though not a designer, I know my way around CSS and can play with Photoshop tools in order to render my imagination.
                                        Also, I click really good photographs.
                                    </p>
                                </div>
                            </div>
                            <div className="row pt-5" id="howDoIDoIt">
                                <div className="col-12">
                                    <h2>How do I do it?</h2>
                                    <p>
                                        A relaxed mind with a pinch of creativity and (more importantly) the following technology stack help me achieve what I aspire.
                                    </p>
                                </div>
                                {
                                    this.renderIcons()
                                }
                            </div>
                            <div className="row pt-5" id="experience">
                                <div className="col-12">
                                    <h2>How experienced am I?</h2>
                                    <p>
                                        I am a frontend developer at <a href="https://www.turningcloud.com" target="_blank" rel="noopener noreferrer">TurningCloud Solutions Private Limited</a> since October 2020.
                                        Using ReactJS and Redux, I work on development of modules for their product <a href="https://www.supplymint.com/" target="_blank" rel="noopener noreferrer">Supplymint</a>;
                                        integrating REST APIs and creating flawless UX. Besides, I have certificates from Coursera for courses on Bootstrap, ReactJS and React Native.
                                        While undergoing these courses, I built the following (toy) projects.<span className="fa fa-external-link-alt"></span>
                                    </p>
                                </div>
                                {
                                    this.renderProjects()
                                }
                                <div className="col-12 pt-3">
                                    <p>
                                        In case you want to take a look at the certificates-
                                    </p>
                                    <div className="accordion shadow">{
                                        this.renderCertificates()
                                    }</div>
                                </div>
                            </div>
                            <div className="row d-md-none pt-5 mb-0 connect-icons-small">
                                <a href="https://github.com/ekagra25" target="_blank" rel="noopener noreferrer" style={{color: "#000000"}}><span className="fa fa-github-square"></span></a>
                                <a href="https://www.linkedin.com/in/ekagra-midha/" target="_blank" rel="noopener noreferrer" style={{color: "#0c66c2"}}><span className="fa fa-linkedin-square"></span></a>
                                <a href="https://www.facebook.com/ekagra.midha.3/" target="_blank" rel="noopener noreferrer" style={{color: "#1877f2"}}><span className="fa fa-facebook-square"></span></a>
                            </div>
                            <hr />
                            <div className="row">
                                <div className="col-12 d-flex justify-content-center mb-5">
                                    <Link to="invisible-div" spy={true} smooth={true} duration={1000} style={{color: "#ffffff"}}><Button id="backToTop">Back to top <span className="fa fa-chevron-circle-up fa-lg"></span></Button></Link>
                                </div>
                            </div>
                        </div>
                        <div id="navbar" className="d-none d-md-flex col-md-3">
                            <div className="row">
                                <Link className="navbar-item" activeClass="active" to="whoAmI" spy={true} smooth={true} duration={1000}>
                                    Who am I?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="education" spy={true} smooth={true} duration={1000}>
                                    My education?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="achievements" spy={true} smooth={true} duration={1000}>
                                    My achievements?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="whatDoIDo" spy={true} smooth={true} duration={1000}>
                                    What do I do?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="howDoIDoIt" spy={true} smooth={true} duration={1000}>
                                    How do I do it?
                                </Link>
                                </div>
                                <div className="row">
                                <Link className="navbar-item" activeClass="active" to="experience" spy={true} smooth={true} duration={1000}>
                                    How experienced am I?
                                </Link>
                            </div>
                            <div className="row connect-icons">
                                <a href="https://github.com/ekagra25" target="_blank" rel="noopener noreferrer"><span id="github" className="fa fa-github-square connect"></span></a>
                                <a href="https://www.linkedin.com/in/ekagra-midha/" target="_blank" rel="noopener noreferrer"><span id="linkedin" className="fa fa-linkedin-square connect"></span></a>
                                <a href="https://www.facebook.com/ekagra.midha.3/" target="_blank" rel="noopener noreferrer"><span id="facebook" className="fa fa-facebook-square connect"></span></a>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
    
}

export default Main;